    </div>

    <!-- Ëä±Âõ≠ÂõæÈâ¥Âç°ÁâáÊ®°ÊÄÅÊ°Ü -->
    <div v-if="showGardenCard" class="card-modal" @click="closeGardenCard">
      <div class="garden-card-container" @click.stop>
        
        <!-- Âç°ÁâáË£ÖÈ•∞ËæπÊ°Ü -->
        <div class="card-border-decoration">
          <img src="/assets/images/card-border.png" alt="" class="border-frame">
        </div>
        
        <div class="garden-card-content" ref="gardenCardContent">
          <div class="card-header">
            <h2>üå∏ ÊàëÁöÑÊÄùÁª¥Ëä±Êúµ</h2>
            <span class="card-date">{{ formatDate(new Date()) }}</span>
          </div>
          
          <div class="card-flower-display">
            <img :src="getFlowerBloomImage()" alt="ÊàëÁöÑËä±Êúµ" class="card-flower-image">
          </div>
          
          <div class="card-journey-summary">
            <div class="journey-item">
              <strong>üå± Áßç‰∏ãÁöÑÁßçÂ≠ê:</strong>
              <p>{{ formData.situation || '‰∏Ä‰∏™ÁâπÂà´ÁöÑÊó∂Âàª' }}</p>
            </div>
            <div class="journey-item">
              <strong>üåø ÂàùÂßãÁöÑÊÉ≥Ê≥ï:</strong>
              <p>{{ formData.automaticThought || 'ÂÜÖÂøÉÁöÑÂ£∞Èü≥' }}</p>
            </div>
            <div class="journey-item">
              <strong>‚ú® Êñ∞ÁöÑÁêÜËß£:</strong>
              <p>{{ formData.alternativeThought || 'Êõ¥Ê∏©ÂíåÁöÑËßÜËßí' }}</p>
            </div>
            <div class="journey-item">
              <strong>üå∏ ÊúÄÁªàÁöÑÊÑüÂèó:</strong>
              <p>{{ selectedEmotion?.name || 'Âπ≥Èùô' }} ({{ formData.intensity }}%)</p>
            </div>
          </div>
          
          <div class="card-blessing">
            <p>üíù ÊØè‰∏ÄÊ¨°ÊàêÈïøÈÉΩÂÄºÂæóË¢´ËÆ∞ÂΩïÂíåÁèçËóè</p>
          </div>
        </div>
        
        <div class="card-actions">
          <button @click="downloadGardenCard" class="download-btn">
            üíæ ‰øùÂ≠òÂà∞Áõ∏ÂÜå
          </button>
          <button @click="shareGardenCard" class="share-btn">
            üì§ ÂàÜ‰∫´
          </button>
          <button @click="closeGardenCard" class="close-btn">
            ÂÖ≥Èó≠
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'ThoughtGardenFinal',
  data() {
    return {
      // Ê∏∏ÊàèÁä∂ÊÄÅ
      gameStage: 'start', // start, situation, thought, alternative, feeling, complete
      plantStage: 0, // 0-4
      showThorns: false,
      showPollen: false,
      isFullyBloomed: false,
      showGardenCard: false,
      showGuidance: true,
      
      // ÈÄâÊã©Áä∂ÊÄÅ
      selectedEmotion: null,
      
      // Ë°®ÂçïÊï∞ÊçÆ
      formData: {
        situation: '',
        automaticThought: '',
        alternativeThought: '',
        intensity: 70,
        timestamp: null
      },
      
      // ÊÉÖÁª™ÈÄâÈ°πÔºà‰ΩøÁî®ÁúüÂÆûÂõæÁâáÔºâ
      emotionOptions: [
        { 
          name: 'Âπ≥Èùô', 
          icon: '/assets/images/emotion-calm.png',
          color: '#84A98C',
          flowerType: 'blue'
        },
        { 
          name: 'ÊÑâÊÇ¶', 
          icon: '/assets/images/emotion-happy.png',
          color: '#FFC857',
          flowerType: 'yellow'
        },
        { 
          name: 'Â∏åÊúõ', 
          icon: '/assets/images/emotion-hope.png',
          color: '#FF9B85',
          flowerType: 'red'
        },
        { 
          name: 'ÂÆâËØ¶', 
          icon: '/assets/images/emotion-peaceful.png',
          color: '#CAD2C5',
          flowerType: 'blue'
        }
      ],
      
      // ÊèêÁ§∫ÂíåÂºïÂØº
      guidanceMessages: {
        situation: 'üí≠ ÊèèËø∞ÂÆåÊÉÖÂÜµÂêéÔºåÁÇπÂáªÊ§çÁâ©ÊàñÊåâÁªßÁª≠ÈîÆ...',
        thought: 'üå± ÂÜô‰∏ãÊÉ≥Ê≥ïÔºåÁúãÂπºËäΩÂ¶Ç‰ΩïÁ†¥ÂúüËÄåÂá∫...',
        alternative: '‚ú® ÈáçÊñ∞ÊÄùËÄÉÊó∂ÔºåÊ≥®ÊÑèÊ§çÁâ©ÁöÑÁ•ûÂ•áÂèòÂåñ...',
        feeling: 'üå∏ ÈÄâÊã©ÊÉÖÁª™ÔºåËßÅËØÅËä±ÊúµÁªΩÊîæÁöÑÂ•áËøπ...'
      },
      
      hintMessages: {
        1: 'ÁßçÂ≠êÂ∑≤ÁªèÁßç‰∏ãÔºåÈùôÂæÖÁ†¥Âúü üå±',
        2: 'ÂπºËäΩÁ†¥ÂúüËÄåÂá∫ÔºåÂ∏¶ÁùÄÂ∞ñÈîêÁöÑÊÉ≥Ê≥ï üåø',
        3: 'Ê§çÁâ©ÂºÄÂßãËåÅÂ£ÆÊàêÈïøÔºåÂèòÂæóÊõ¥Âä†ÂÅ•Â∫∑ üçÉ',
        4: 'Ëä±ËãûÊ≠£Âú®ÂΩ¢ÊàêÔºåÂç≥Â∞ÜÁªΩÊîæ üå∏'
      }
    }
  },
  computed: {
    canProceed() {
      switch (this.gameStage) {
        case 'situation': return this.formData.situation.trim().length > 5
        case 'thought': return this.formData.automaticThought.trim().length > 3
        case 'alternative': return this.formData.alternativeThought.trim().length > 3
        case 'feeling': return this.selectedEmotion !== null
        default: return false
      }
    },
    
    currentHint() {
      return this.hintMessages[this.plantStage] || ''
    },
    
    currentGuidance() {
      return this.guidanceMessages[this.gameStage] || ''
    }
  },
  methods: {
    // Ê∏∏ÊàèÊµÅÁ®ãÊéßÂà∂
    startJourney() {
      this.gameStage = 'situation'
      this.plantStage = 1
      this.formData.timestamp = new Date()
    },
    
    proceedToNextStep() {
      if (!this.canProceed) return
      
      switch (this.gameStage) {
        case 'situation':
          this.gameStage = 'thought'
          this.plantStage = 2
          this.showThorns = true
          break
        case 'thought':
          this.gameStage = 'alternative'
          this.plantStage = 3
          this.showThorns = false
          break
        case 'alternative':
          this.gameStage = 'feeling'
          this.plantStage = 4
          break
      }
    },
    
    goToPreviousStep() {
      switch (this.gameStage) {
        case 'thought':
          this.gameStage = 'situation'
          this.plantStage = 1
          this.showThorns = false
          break
        case 'alternative':
          this.gameStage = 'thought'
          this.plantStage = 2
          this.showThorns = true
          break
        case 'feeling':
          this.gameStage = 'alternative'
          this.plantStage = 3
          this.showThorns = false
          break
      }
    },
    
    completeJourney() {
      if (!this.selectedEmotion) return
      
      this.isFullyBloomed = true
      this.showPollen = true
      this.gameStage = 'complete'
      
      // ‰øùÂ≠òËÆ∞ÂΩïÂà∞store
      this.saveToStore()
      
      // ÈöêËóèÂºïÂØº
      this.showGuidance = false
    },
    
    startNewJourney() {
      // ÈáçÁΩÆÊâÄÊúâÁä∂ÊÄÅ
      this.gameStage = 'start'
      this.plantStage = 0
      this.showThorns = false
      this.showPollen = false
      this.isFullyBloomed = false
      this.selectedEmotion = null
      this.showGuidance = true
      
      this.formData = {
        situation: '',
        automaticThought: '',
        alternativeThought: '',
        intensity: 70,
        timestamp: null
      }
    },
    
    // ‰∫§‰∫íÂ§ÑÁêÜ
    handlePlantInteraction() {
      // ÁÇπÂáªÊ§çÁâ©Êé®ËøõÊµÅÁ®ã
      if (this.canProceed) {
        this.proceedToNextStep()
      }
    },
    
    handleSituationInput() {
      // Ëá™Âä®Êé®Ëøõ
      if (this.formData.situation.trim().length > 10) {
        setTimeout(() => {
          if (this.gameStage === 'situation') {
            this.proceedToNextStep()
          }
        }, 1500)
      }
    },
    
    handleThoughtInput() {
      if (this.formData.automaticThought.trim().length > 8) {
        setTimeout(() => {
          if (this.gameStage === 'thought') {
            this.proceedToNextStep()
          }
        }, 1500)
      }
    },
    
    handleAlternativeInput() {
      if (this.formData.alternativeThought.trim().length > 8) {
        setTimeout(() => {
          if (this.gameStage === 'alternative') {
            this.proceedToNextStep()
          }
        }, 1500)
      }
    },
    
    selectEmotion(emotion) {
      this.selectedEmotion = emotion
      this.updateFlowerAppearance()
      
      // Âª∂ËøüÂÆåÊàêÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÂèòÂåñ
      setTimeout(() => {
        this.completeJourney()
      }, 2000)
    },
    
    updateFlowerAppearance() {
      // Ëä±ÊúµÂ§ñËßÇ‰ºöÊ†πÊçÆÊÉÖÁª™ÂíåÂº∫Â∫¶ÂèòÂåñ
      // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†Êõ¥Â§öËßÜËßâÂèçÈ¶à
    },
    
    // ÂõæÁâáËµÑÊ∫êÊñπÊ≥ï
    getFlowerBudImage() {
      if (!this.selectedEmotion) return '/assets/images/blue-flower-bud.png'
      return `/assets/images/${this.selectedEmotion.flowerType}-flower-bud.png`
    },
    
    getFlowerBloomImage() {
      if (!this.selectedEmotion) return '/assets/images/blue-flower-bloom.png'
      return `/assets/images/${this.selectedEmotion.flowerType}-flower-bloom.png`
    },
    
    // Á≤íÂ≠êÊïàÊûúÊ†∑ÂºèÁîüÊàê
    getGrowthSparkleStyle(index) {
      const angle = (index * 45) + (Math.random() * 30 - 15)
      const distance = 50 + Math.random() * 40
      const x = Math.cos(angle * Math.PI / 180) * distance
      const y = Math.sin(angle * Math.PI / 180) * distance
      const delay = Math.random() * 3
      const duration = 2 + Math.random() * 2
      
      return {
        left: `calc(50% + ${x}px)`,
        top: `calc(50% + ${y}px)`,
        animationDelay: `${delay}s`,
        animationDuration: `${duration}s`
      }
    },
    
    getPollenStyle(index) {
      const angle = (index * 24) + (Math.random() * 20 - 10)
      const distance = 30 + Math.random() * 50
      const x = Math.cos(angle * Math.PI / 180) * distance
      const y = Math.sin(angle * Math.PI / 180) * distance
      const delay = Math.random() * 2
      const duration = 4 + Math.random() * 2
      
      return {
        left: `calc(50% + ${x}px)`,
        top: `calc(50% + ${y}px)`,
        animationDelay: `${delay}s`,
        animationDuration: `${duration}s`
      }
    },
    
    // Ëä±Âõ≠ÂõæÈâ¥Áõ∏ÂÖ≥
    generateGardenCard() {
      this.showGardenCard = true
    },
    
    closeGardenCard() {
      this.showGardenCard = false
    },
    
    async downloadGardenCard() {
      try {
        if (typeof html2canvas === 'undefined') {
          alert('ÂõæÁâáÁîüÊàêÂäüËÉΩÊöÇ‰∏çÂèØÁî®ÔºåËØ∑Âà∑Êñ∞È°µÈù¢ÈáçËØï')
          return
        }
        
        const canvas = await html2canvas(this.$refs.gardenCardContent)
        const link = document.createElement('a')
        link.download = `ÊÄùÁª¥Ëä±Âõ≠_${this.formatDate(new Date())}.png`
        link.href = canvas.toDataURL()
        link.click()
      } catch (error) {
        console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
        alert('‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï')
      }
    },
    
    shareGardenCard() {
      if (navigator.share) {
        navigator.share({
          title: 'ÊàëÁöÑÊÄùÁª¥Ëä±Âõ≠',
          text: 'ÊàëÂàöÂàöÂÆåÊàê‰∫Ü‰∏ÄÊ¨°ÊÄùÁª¥Ê†ΩÂüπ‰πãÊóÖÔºÅ',
          url: window.location.href
        })
      } else {
        // Â§áÁî®ÂàÜ‰∫´ÊñπÊ°à
        alert('ËØ∑‰ΩøÁî®‰∏ãËΩΩÂäüËÉΩ‰øùÂ≠òÂõæÁâáÂêéÊâãÂä®ÂàÜ‰∫´')
      }
    },
    
    // Êï∞ÊçÆ‰øùÂ≠ò
    saveToStore() {
      const record = {
        situation: this.formData.situation,
        automaticThought: this.formData.automaticThought,
        // Ê∑ªÂä†ËÆ§Áü•ÂàÜÊûêÊï∞ÊçÆÁªìÊûÑÂÖºÂÆπ
        supportingEvidence: '', // ÊöÇÊó∂‰∏∫Á©∫Ôºå‰øùÊåÅÂÖºÂÆπÊÄß
        contradictoryEvidence: '', // ÊöÇÊó∂‰∏∫Á©∫Ôºå‰øùÊåÅÂÖºÂÆπÊÄß
        alternativeThought: this.formData.alternativeThought,
        emotions: this.selectedEmotion ? [this.selectedEmotion.name] : [],
        intensity: this.formData.intensity,
        reevaluatedEmotions: this.selectedEmotion ? [this.selectedEmotion.name] : [],
        newEmotionIntensity: this.formData.intensity,
        timestamp: this.formData.timestamp || new Date(),
        // Ëä±Âõ≠ÁâπÊúâÊï∞ÊçÆ
        emotion: this.selectedEmotion,
        flowerType: this.selectedEmotion?.flowerType,
        id: Date.now(),
        completed: true,
        gardenVersion: true // Ê†áËÆ∞‰∏∫Ëä±Âõ≠ÁâàÊú¨ËÆ∞ÂΩï
      }
      
      // ÈõÜÊàêÂà∞Áé∞ÊúâÁöÑstoreÁ≥ªÁªü
      if (this.$store) {
        this.$store.state.thoughtRecords.unshift(record)
        this.$store.saveState()
      }
    },
    
    viewProgress() {
      // Ë∑≥ËΩ¨Âà∞ËÆ∞ÂΩïÈ°µÈù¢
